.content-home .card {
    padding-bottom: 4rem;
    border: 0;
    color: #fff;
    text-shadow: 1px 1px 1px #000;
    font-weight: 300;
    border-radius: 0.25rem;
}
.content-home .nav-link { height: 100%; }
.content-home .card-title { color: #fff; font-size: 3rem; border-color: rgba(255, 255, 255, 0.5); }
.content-home p { font-size: 1.25rem; }

.content-home .card.process-optimization {
    background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url("/img/precision.png");
    background-size: cover;
}
.content-home .card.traceability {
    background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url("/img/heavy.png");
    background-size: cover;
}
