/* Provide sufficient contrast against white background */
a {
  color: #0366d6;
}

code {
  color: #E01A76;
}

.btn-primary {
  color: #fff;
  background-color: #1b6ec2;
  border-color: #1861ac;
}

.custom-control-input:checked ~ .custom-control-label::before {
  color: #fff;
  border-color: #c82333;
  background-color: #bd2130;
}

/* General */
html { height: 100%; }
body { height: 100%; background-color: #f3f3f4; color: #676a6c; font-size: 0.9rem; }
#root { position: relative; min-height: 100vh; }
#breadcrumb-wrap { background-color: white; }
#content-wrap { padding-bottom: 3.5rem; }
header { font-size: 1rem; }
footer { background-color: white; position: absolute; bottom: 0; width: 100%; height: 2.5rem; }

h1 { color: #c00; font-size: 2rem; font-weight: 100; }
h4 { font-weight: 100; }

button a, button a:hover, button a:focus { color: inherit; text-decoration: none; }

.auto          { cursor: auto; }
.default       { cursor: default; }
.none          { cursor: none; }
.context-menu  { cursor: context-menu; }
.help          { cursor: help; }
.pointer       { cursor: pointer; }
.progress      { cursor: progress; }
.wait          { cursor: wait; }
.cell          { cursor: cell; }
.crosshair     { cursor: crosshair; }
.text          { cursor: text; }
.vertical-text { cursor: vertical-text; }
.alias         { cursor: alias; }
.copy          { cursor: copy; }
.move          { cursor: move; }
.no-drop       { cursor: no-drop; }
.not-allowed   { cursor: not-allowed; }
.all-scroll    { cursor: all-scroll; }
.col-resize    { cursor: col-resize; }
.row-resize    { cursor: row-resize; }
.n-resize      { cursor: n-resize; }
.e-resize      { cursor: e-resize; }
.s-resize      { cursor: s-resize; }
.w-resize      { cursor: w-resize; }
.ns-resize     { cursor: ns-resize; }
.ew-resize     { cursor: ew-resize; }
.ne-resize     { cursor: ne-resize; }
.nw-resize     { cursor: nw-resize; }
.se-resize     { cursor: se-resize; }
.sw-resize     { cursor: sw-resize; }
.nesw-resize   { cursor: nesw-resize; }
.nwse-resize   { cursor: nwse-resize; }

/* Bootstrap components */
.alert { width: 100%; }

.breadcrumb { background-color: transparent; }
.breadcrumb-item.active { color: inherit; }
.breadcrumb-item a { color: #c00; }

.card { height: 100%; width: 100%; border: 0; border-top: solid 1px rgba(0, 0, 0, 0.125); border-radius: 0;}
.card-title { color: #c00; font-size: 1.25rem; font-weight: 100; border-bottom: solid 1px rgba(0, 0, 0, 0.125); }
.card-body { padding: 1rem; }
.card-text { margin-bottom: 0.4rem; }
.card-img { width: auto; height: 150px; display: block; margin-left: auto; margin-right: auto; }

.dropdown-item.active, .dropdown-item:active {
  background-color: #c00;
}

.form-control { color: #676a6c; font-size: 0.9rem; }

.jumbotron { width: 100%; border-radius: 0; }

.list-group-item:first-child { border-radius: 0; }
.list-group-item:last-child { border-radius: 0; }

.page-link {
  color: #c00;
}
.page-link:hover {
  color: #8e0000;
}
.page-item.active .page-link {
  background-color: #c00;
  border-color: #c00;
}

.table { text-align: center; }
.table th { color: #c00; font-weight: 400; border-top: none; }
.table tbody { color: #676a6c; }
.table-striped tbody tr:nth-of-type(2n+1) { background-color: #fbfbfb; }

/* Component - Process Optimization */
.card.pr-opti-plot { border: 0; }
.card.pr-opti-plot .card-text { margin-bottom: 0; }
.card.pr-opti-plot .recharts-wrapper { margin-top: 1rem; }

.jumbotron.pr-opti { background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url("/img/precision.png"); background-size: cover; }
.jumbotron.pr-opti h1,
.jumbotron.pr-opti p { color: #fff; text-shadow: 1px 1px 1px #000; }
.jumbotron.pr-opti hr { border-color: rgba(255, 255, 255, 0.5); }
.jumbotron.pr-opti .btn-outline-primary { color: #fff; border-color: #fff; }
.jumbotron.pr-opti .btn-outline-primary:hover { color: #121416; border-color: #fff; background-color: #fff; }

/* Component - Stations */
.station-list .level-0 {
  background-color: #505050;
  color: white;
  font-weight: bold;
  text-transform: uppercase;
}

.station-list .level-1 {
  background-color: #f3f3f4;
  font-weight: bold;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

.station-list .level-2 {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  padding-left: 1.75rem;
}

/* Custom style */
.box-shadow { box-shadow: 0 .25rem .75rem rgba(0, 0, 0, .05); }
.fa { color: #555; }
.power-value { color: #1861ac; }
.temp-value { color: #c00; }
